@import url("https://fonts.googleapis.com/css2?family=Roboto:wght@300;700&display=swap");
/*       Fonts       */
/*       Colors       */
html {
  font-size: 16px;
}

.module {
  display: flex;
  margin-top: 96px;
  max-width: 1440px;
  margin-left: auto;
  margin-right: auto;
}

.module > .rightSide {
  margin-top: auto;
  margin-bottom: auto;
  margin-left: 95px;
  max-width: 380px;
}

.module > .rightSide > h1 {
  font-family: "Roboto", sans-serif;
  font-weight: 700;
  font-size: 2.625rem;
  color: #121212;
}

.module > .rightSide > p {
  font-family: "Roboto", sans-serif;
  color: #121212;
  line-height: 24px;
}

.generalBtn {
  font-family: "Roboto", sans-serif;
  background-color: #E4C1AE;
  color: #FFFFFF;
  border-radius: 32px;
  border-width: 0px;
  width: 128px;
  height: 48px;
  font-weight: 700;
  font-size: 1rem;
  line-height: 16px;
}
.generalBtn:hover {
  outline: 1px, solid, #121212;
  color: #e6e6e6;
}
.generalBtn:active {
  background-color: #d9a78c;
  color: #bfbfbf;
  outline: 1px, solid, #121212;
}